import { Component, OnInit } from '@angular/core';
import { JobService } from 'src/app/service/job.service';
import { Candidate } from 'src/app/models/candidate';

@Component({
  selector: 'app-request-candidate',
  templateUrl: './request-candidate.component.html',
  styleUrls: ['./request-candidate.component.scss']
})
export class RequestCandidateComponent implements OnInit {

  colsCandidate:any[];
  listCandidate:Candidate[]=[

  ]
  listRequetCV:any[]=[];
  constructor(private jobService:JobService) { }

  ngOnInit() {
   // alert('ts');
    this.jobService.getRequesetCV()
    .subscribe(res =>{
      this.listRequetCV=res;
      
    });

    this.colsCandidate=[
      { field: 'id', header: 'STT' },
      { field: 'email', header: 'Email' },
      { field: 'experience', header: 'Kinh Nghiem' },
      { field: 'idJob', header: 'idJob' },
      { field: 'idUser', header: 'idUser' },
    ]
  }



}
