import { Component, OnInit } from '@angular/core';
import { LoginService } from 'src/app/service/login.service';
import { SocketIoService } from 'src/app/service/socket-io.service';
import { forEach } from '@angular/router/src/utils/collection';
import { Router } from '@angular/router';
import { JobService } from 'src/app/service/job.service';


@Component({
  selector: 'app-teacher',
  templateUrl: './teacher.component.html',
  styleUrls: ['./teacher.component.scss']
})
export class TeacherComponent implements OnInit {
  name:string;
  request:any=[];
  thongTin:string="";
  
  number:Number;
  soLuong:string="";
  listRequetCV:any[]=[];
  constructor(private jobService:JobService,private router: Router,private soketIo:SocketIoService,private loginServe:LoginService) { }

  ngOnInit() {
    this.name=this.loginServe.getName();
    this.soketIo.listen("ServerRequest").subscribe(data=>
      {
          // this.request=data;
          // console.log(data);
          //  for (let i = 0; i < this.request.length; i++) {
          //   this.thongTin+=this.request[i]+",";
          // }
          this.soLuong=data+"";
          localStorage.setItem("duLieu",this.soLuong);
      });
      this.soLuong=localStorage.getItem("duLieu");
   }

   XemYeuCau()
   {
      localStorage.setItem("duLieu","0");
      this.soLuong=0+"";
      this.router.navigate(['teacher/requestCandidate']);
   }
   getRequestCV()
   {
     
     this.jobService.getRequestCandidate()
     .subscribe(res =>{
       this.listRequetCV=res;
       console.log(this.listRequetCV);
     });
   }

}
